import React from 'react';
import Config from '../config';
import ProductsPanel from './ProductsPanel';
import FilterPanel from './FilterPanel';
import { connect } from 'react-redux';
import loadProducts from '../redux/actions/loadProducts';
import setPages from '../redux/actions/setPages';
import Pagination from './Pagination';

class LentesContacto extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            pages: 0
        }
    }

    componentDidMount() {
        let {match:{params}} = this.props;
        fetch(`${Config.host}/lentesdecontacto/${params.page}`)
            .then(response => response.json())
            .then(json => {
                if (json.code === 1) {
                    this.setState({ loading: false });
                    this.props.loadProducts(json.data);
                    this.props.setPages(json.pages);
                }
            })
    }

    render() {
        let {match: {params}} = this.props;
        return (
            <>
                <FilterPanel path='lentesdecontacto' />
                <div className='maincontainer'>
                    <ProductsPanel loading={this.state.loading} title='Lentes De Contacto' />
                    <Pagination path='lentesdecontacto' page={params.page}></Pagination>
                </div>
            </>
        )
    }
}

const mapStateToProps = state => {
    return {
        products: state.products
    }
}

const mapDispatchToProps = {
    loadProducts,
    setPages
}

export default connect(mapStateToProps, mapDispatchToProps)(LentesContacto)