import React from 'react';
import {connect} from 'react-redux';
import toggleModal from '../redux/actions/toggleModal';

class FloatCartButton extends React.Component{
    render(){
        return (
            <div onClick={() => this.props.toggleModal(true)} className='float-cart-button'>
                <i className='fa fa-shopping-cart'></i>
                <span style={{
                    width: '40%',
                    height: '40%',
                    background: 'red', 
                    borderRadius: '100%',
                    marginRight: '10px',
                    justifyContent: 'center',
                    alignItems: 'center',
                    display: 'flex',
                    position: 'absolute',
                    top: '-5px',
                    right: '-5px'
                }}>{this.props.cart && this.props.cart.length ? this.props.cart.length:0}</span>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        cart: state.cart.cart
    }
}

const mapDispatchToProps = {
    toggleModal
}

export default connect(mapStateToProps, mapDispatchToProps)(FloatCartButton);