import React from 'react';
import Config from '../config';
import ProductsPanel from './ProductsPanel';
import FilterPanel from './FilterPanel';
import loadProducts from '../redux/actions/loadProducts'
import setPages from '../redux/actions/setPages'
import setPath from '../redux/actions/setPath'
import { connect } from 'react-redux'
import Pagination from './Pagination'

class GotasHidratantes extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: true
        }
    }

    componentDidMount() {
        let {match:{params}} = this.props;
        this.props.setPath(`${Config.host}/gotashidratantes`)
        fetch(`${Config.host}/gotashidratantes/${params.page}`)
            .then(response => response.json())
            .then(json => {
                if (json.code === 1) {
                    this.setState({ loading: false });
                    this.props.loadProducts(json.data)
                    this.props.setPages(json.pages)
                }
            })
            .catch(err => console.log(err.message));
    }

    render() {
        let {match:{params}} = this.props;
        return (
            <>
                <FilterPanel path='gotashidratantes' />
                <div className='maincontainer'>
                    <ProductsPanel loading={this.state.loading} title='Gotas Hidratantes' />
                    <Pagination path='gotashidratantes' page={params.page} />
                </div>
            </>
        )
    }
}

const mapDispatchToProps = {
    loadProducts,
    setPages,
    setPath
}

export default connect(null, mapDispatchToProps)(GotasHidratantes)