import React from 'react';
import Config from '../config';
import loadProducts from '../redux/actions/loadProducts';
import setPath from '../redux/actions/setPath';
import setPages from '../redux/actions/setPages'
import setPage from '../redux/actions/setPage'
import {connect} from 'react-redux'

class FilterPanel extends React.Component {


    constructor(props){
        super(props);
        this.state = {
            loading: true,
            marcas: [],
            laboratorios: [],
            vconditions: []
        }
    }

    componentDidMount(){
        fetch(`${Config.host}/${this.props.path}s/marcas`)
        .then(response => response.json())
        .then(json => {
            if (json.code === 1) {
                this.setState({loading: false, marcas: json.data});
            }
        });

        fetch(`${Config.host}/lentesdecontacto/laboratorios`)
        .then(response => response.json())
        .then(json => {
            if(json.code === 1){
                this.setState({laboratorios: json.data});
            }
        })
        fetch(`${Config.host}/lentesdecontacto/vconditions`)
        .then(response => response.json())
        .then(json => {
            if(json.code === 1){
                this.setState({vconditions: json.data});
            }
        })
    }


    _showFilters() {
        document.getElementsByClassName('filter-container')[0].classList.toggle('hide');
    }

    _loadProductsByTradeMark(trademark) {
        this.props.setPath(`${Config.host}/${this.props.path}/marcas/${trademark}`);
        fetch(`${Config.host}/${this.props.path}/marcas/${trademark}/1`)
            .then(response => response.json())
            .then(json => {
                if (json.code === 1) {
                    this.props.loadProducts(json.data);
                    this.props.setPages(json.pages);
                    this.props.setPage(1)
                }
            })
    }

    _loadProductsByLaboratory(laboratory){
        this.props.setPath(`${Config.host}/${this.props.path}/laboratorios/${laboratory}`);
        fetch(`${Config.host}/${this.props.path}/laboratorios/${laboratory}/1`)
        .then(response => response.json())
        .then(json => {
            if(json.code === 1){
                this.props.loadProducts(json.data);
                this.props.setPages(json.pages)
                this.props.setPage(1)
            }
        })
    }

    _loadProductsByVisualCondition(vcond){
        this.props.setPath(`${Config.host}/${this.props.path}/vconds/${vcond}`);
        fetch(`${Config.host}/${this.props.path}/vconds/${vcond}/1`)
        .then(response => response.json())
        .then(json => {
            if(json.code === 1){
                this.props.loadProducts(json.data);
                this.props.setPages(json.pages)
                this.props.setPage(1)
            }
        })
    }

    _loadAll(){
        this.props.setPath(`${Config.host}/${this.props.path}`)
        fetch(`${Config.host}/${this.props.path}/1`)
        .then(response => response.json())
        .then(json => {
            this.props.loadProducts(json.data);
            this.props.setPages(json.pages)
            this.props.setPage(1)
        })
    }

    render(){
        return (
            <div className='filter'>
                <header className='filter-header' onClick={this._showFilters}>
                    <b>Filtrar por:</b>
                </header>
    
                <div className='filter-container hide'>
                    <div>
                        <ul className='list'>
                            <li onClick={() => this._loadAll()}>Todos</li>
                        </ul>
                    </div>
                    <div>
                        <b className='filter-title'>Marcas:</b>
                        <br />
                        {this.state.loading ? 'cargando...' :
                            <ul className='list'>
                                {this.state.marcas.map(marca => {
                                    return <li onClick={() => { this._loadProductsByTradeMark(marca.trademark) }}>{marca.trademark}</li>
                                })}
                            </ul>
                        }
                    </div>
                    {this.props.path === 'lentesdecontacto' ?
                        <div className='lefilter'>
                            <div>
                                <b className='filter-title'>Laboratorios:</b>
                                <br />
                                <ul className='list'>
                                    {
                                        this.state.laboratorios.map(lab => {
                                            return <li onClick={() => this._loadProductsByLaboratory(lab.laboratory)}>{lab.laboratory}</li>
                                        })
                                    }
                                </ul>
                            </div>
                            <div>
                                <b className='filter-title'>Condicion visual:</b>
                                <br />
                                <ul className='list'>
                                    {
                                        this.state.vconditions.map(vcond => {
                                            return <li onClick={() => {this._loadProductsByVisualCondition(vcond.visualcondition)}}>{vcond.visualcondition}</li>
                                        })
                                    }
                                </ul>
                            </div>
                        </div>
                        :
                        null
                    }
                </div>
            </div>
        )
    }
}

const mapDispatchToProps = {
    loadProducts,
    setPath,
    setPages,
    setPage
}

export default connect(null, mapDispatchToProps)(FilterPanel)