import React from 'react';
import {connect} from 'react-redux';
import loadProducts from '../redux/actions/loadProducts'
import setPages from '../redux/actions/setPages';
import setPage from '../redux/actions/setPage'

class Pagination extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            anchors: [],
            active: 1
        }
        this._handleClick = this._handleClick.bind(this);
        this._renderPagination = this._renderPagination.bind(this);
    }

    _handleClick(page){
        this.setState({active: page})
        this.props.setPage(page);
        window.scrollTo(0,0)
        fetch(this.props.path + `/${page}`)
        .then(response => response.json())
        .then(json => {
            if(json.code === 1){
                this.props.loadProducts(json.data);
                this.props.setPages(json.pages);
            }
        })
        .catch(err => console.log(err.message))
    }

    _renderPagination(){
        let anchors = [];
        if(this.props.pages > 1){
            for(let i = 1;i <= this.props.pages;i++){
                anchors.push(
                    <li className={`page-item ${this.props.page === i ? 'active':''}`} key={i}>
                        <a className='page-link' href onClick={() => this._handleClick(i)}>{i}</a>
                    </li>
                )
            }
        }
        return anchors;
    }

    componentDidMount(){
        this.props.setPage(1)
    }

    render(){
        console.log(this.props.pages);
        return(
            <div className='pagination-container'>
                <ul className='pagination'>
                    {this._renderPagination()}
                </ul>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        pages: state.pages.pages,
        path: state.path.path,
        page: state.page.page
    }
}

const mapDispatchToProps = {
    loadProducts,
    setPages,
    setPage
}

export default connect(mapStateToProps, mapDispatchToProps)(Pagination);