import React from 'react';

import {
    Card,
    CardBody,
    CardImg,
    Button,
    CardFooter
} from 'reactstrap'

import addProductToCart from '../redux/actions/addProductToCart';
import {connect} from 'react-redux';


class ProductCard extends React.Component {
    constructor(props){
        super(props);
        if(!this.props.cart){
            this.props.addProductToCart([]);
        }
        this._verifiProductInCart = this._verifiProductInCart.bind(this);
    }
    _addProductToCart(product){
        /*if(sessionStorage.getItem('cart')){
            let cart = JSON.parse(sessionStorage.getItem('cart'));

            sessionStorage.setItem('cart', JSON.stringify([...cart, product]))
        } else {
            sessionStorage.setItem('cart', JSON.stringify([product]))
        }
        console.log(JSON.parse(sessionStorage.getItem('cart')));
        this.props.addProductToCart(JSON.parse(sessionStorage.getItem('cart')).length);
        setTimeout(()=> console.log(this.props.cart))*/
        this.props.addProductToCart([...this.props.cart, product]);
    }

    _verifiProductInCart(product){
        let isInCar = false;
        if (this.props.cart && this.props.cart.length > 0){
            for (let i = 0;i < this.props.cart.length;i++){
                if(this.props.cart[i]['_id'] === product._id){
                    isInCar = true;
                    break;
                }
            }
        }
        return isInCar;
    }

    render() {
        return (
            <Card className='product-card' >
                <CardImg top width='100%' src='https://loremflickr.com/320/240' />
                <CardBody>
                    <span className='product-title'>{this.props.product.name}</span>
                    {this.props.product.discount > 0 ?
                        <div>
                            <span className='product-lastprice'>Descuento:</span> {this.props.product.discount}%<br></br>
                            <span className='product-initprice'>Antes:</span> <s>${this.props.product.initialprice}</s> <br></br>
                            <span className='product-lastprice'>Ahora:</span> ${this.props.product.price}
                        </div>
                        :
                        <div>
                            <span className='product-price'>Precio:</span> ${this.props.product.price}
                        </div>
                    }
                </CardBody>
                <CardFooter className='text-center'>
                    {this._verifiProductInCart(this.props.product) ? 
                        <span style={{color: 'green'}}>Añadido al carrito</span>
                        :<Button onClick={() => this._addProductToCart(this.props.product)} color='primary' block ><i className='fa fa-cart-plus'></i>&nbsp;&nbsp;&nbsp;Añadir al carrito</Button>
                    }
                </CardFooter>
            </Card>
        )
    }
}

const mapStateToProps = state => {
    return {
        cart: state.cart.cart
    }
}

const mapDispatchToProps = {
    addProductToCart
}

export default connect(mapStateToProps, mapDispatchToProps)(ProductCard);