function cart(state={}, action){
    switch(action.type){
        case 'ADD_PRODUCT_TO_CART':
            return {
                ...state,
                cart: [...action.payload]
            }
        case 'DELETE_PRODUCT_IN_CART':
            let cart = state.cart.filter(product => {
                if (product._id === action.payload) return false;
                return true;
            })
            return {
                ...state,
                cart
            }
        default:
            return state;
    }
}

export default cart;