import React  from 'react';
import ProductCard from './ProductCard'
import {connect} from 'react-redux'

class ProductsPanel extends React.Component {

    _renderProducts = () =>{
        let render = undefined;
        if(this.props.products.products && this.props.products.products.length > 0){
            render = this.props.products.products.map(product => {
                return (
                    <ProductCard product={product} />
                )
            })
        } else {
            render = <h2>No hay productos para mostrar</h2>
        }

        return render;
    }

    render(){
        return (
            <div className='container'>
                <header className='header'>
                    {this.props.title}
                </header>
                <div className='products-container'>
                    {this.props.loading ? 'cargando...' : this._renderProducts()
                    }
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        products: state.products
    }
}



export default connect(mapStateToProps)(ProductsPanel)