import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { connect } from 'react-redux';
import toggleModal from '../redux/actions/toggleModal';
import deleteProductInCart from '../redux/actions/deleteProductInCart';

class CartModal extends React.Component {
    constructor(props) {
        super(props);
        this._toggle = this._toggle.bind(this);
        this._renderProducts = this._renderProducts.bind(this);
        this._getTotalPrice = this._getTotalPrice.bind(this);
        console.log(this.props.isOpen)
    }
    _toggle() {
        this.props.toggleModal(!this.props.isOpen);
    }

    _getTotalPrice(){
        let price = 0;
        this.props.cart.forEach(product => {
            price += product.price;
        })
        return price;
    }

    _renderProducts() {
        let products = this.props.cart.map(product => {
            return(
                <div style={{
                    width: '100%',
                    height: '6vh',
                    background: '#ddd',
                    marginTop: '1vh',
                    display: 'flex',
                    alignItems: 'center'
                }}>
                    <img src='https://loremflickr.com/320/240' style={{marginRight:'2%'}} width='10%' height='100%' alt=''/>
                    <div style={{display: 'flex', flexDirection: 'column', width: 'auto', paddingTop: '2px', paddingBottom: '2px'}}>
                        <span><b>Producto: </b>{product.name}</span>
                        <span><b>Precio: $</b>{product.price}</span>
                    </div>
                    <div onClick={() => this.props.deleteProductInCart(product)} style={{
                        position: 'absolute',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        right: '2%',
                        width: '7%',
                        height: '6vh',
                        background: '#ff2222',
                        cursor: 'pointer'
                    }}>
                        <span className='fa fa-times'></span>
                    </div>
                </div>
            )
        })

        return products
    }
    render() {
        return (
            <div>
                <Modal isOpen={this.props.isOpen} toggle={this._toggle}>
                    <ModalHeader toggle={this._toggle}>
                        {this.props.cart && this.props.cart.length ? this.props.cart.length:0}&nbsp;
                        {this.props.cart && this.props.cart.length === 1 ? 'Producto Seleccionado':'Productos Seleccionados'}
                    </ModalHeader>
                    <ModalBody>
                        <div style={{ display: 'flex', flexDirection: 'column' }}>
                            {this.props.cart && this.props.cart.length ? this._renderProducts() : null}
                        </div>
                    </ModalBody>
                    <ModalFooter>
                        {this.props.cart && this.props.cart.length ? <b>Total: ${this._getTotalPrice()}</b>:<b>Total: $0</b>}&nbsp;&nbsp;
                        {this.props.cart && this.props.cart.length > 0 ? 
                            <button className='btn btn-primary'><span className='fa fa-check-circle'></span>&nbsp;Comprar</button>:null
                        }
                    </ModalFooter>
                </Modal>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        isOpen: state.cartModal.isOpen,
        cart: state.cart.cart
    }
}

const mapDispatchToProps = {
    toggleModal,
    deleteProductInCart
}

export default connect(mapStateToProps, mapDispatchToProps)(CartModal);