import React from 'react';
import { Switch, Route} from 'react-router-dom'
import LentesContacto from './Components/LentesContacto'
import './App.css';
import Soluciones from './Components/Soluciones';
import Footer from './Components/Footer';
import Header from './Components/Header'
import GotasHidratantes from './Components/GotasHidratantes';
import CuidadoYBelleza from './Components/CuidadoYBelleza';
import All from './Components/All';
import FloatCartButton from './Components/FloatCartButton';
import CartModal from './Components/CartModal';

function App() {
  return (
    <div className="App">
      <Header />
      <FloatCartButton />
      <CartModal />
        <Switch>
          <div className='mainpanel'>
            <Route exact path='/' component={() => null} />
            <Route exact path='/lentesdecontacto/:page?' component={LentesContacto} />
            <Route exact path='/soluciones/:page?' component={Soluciones} />
            <Route exact path='/gotashidratantes/:page?' component={GotasHidratantes} />
            <Route exact path='/cuidadoybelleza/:page?' component={CuidadoYBelleza} />
            <Route exact path='/todos/:page?' component={All} />
          </div>
        </Switch>
      <Footer />
    </div>
  );
}

export default App;
